@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
#root,
body {
  margin: 0;
  width: 100%;
  height: 100vh;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  margin: 0;
  width: 35%;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.25);
}

.history-field,
.result-field {
  font-family: "Open Sans", "sans-serif";
  text-align: right;
}

.result-field p {
  font-size: 35px;
  font-weight: 600;
  margin: 12px 22px 0 0;
}

.history-field p {
  font-size: 20px;
  font-weight: 500;
  margin: 12px 24px 0 0;
  color: grey;
}

.history-field {
  width: 100%;
  height: 47px;
}

.result-field {
  width: 100%;
  height: 80px;
}

.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 350px;
  padding: 23px 0 0 0;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 55px;
  height: auto;
  align-self: flex-start;
  margin: 0 14px;
}

button {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 17px;
  margin: 8px 0;
  font-family: "Open Sans", "sans-serif";
  cursor: pointer;
  border: none;
  border-radius: 50%;
  font-weight: bolder;
}

.red {
  background: #20b2aa;
}
.green {
  background: #ffa500;
}
.blue {
  background: #f08080;
}
.yellow {
  background: #7d93e0;
}
.pink {
  background: #9477af;
}

